.job {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 800px;
  margin-bottom: 400px;

  background-color: rgb(26, 26, 26);

  &__info {
    padding: 0 15%;
    margin-top: 150px;
    font-size: 24px;

    @include forPhone {
      font-size: 20px;
      margin-top: 50px;
    }
  }
}
